// src/AdminPage.js
import React, { useEffect, useState } from "react";
import { db } from "./firebase";
import { collection, getDocs, query, orderBy } from "firebase/firestore";

export default function AdminPage() {
  const [reservations, setReservations] = useState([]);

  useEffect(() => {
    const fetchReservations = async () => {
      const q = query(collection(db, "reservations"), orderBy("createdAt", "desc"));
      const snapshot = await getDocs(q);
      const data = snapshot.docs.map(doc => ({ id: doc.id, ...doc.data() }));
      setReservations(data);
    };

    fetchReservations();
  }, []);

  return (
    <div className="p-6 max-w-4xl mx-auto">
      <h1 className="text-2xl font-bold mb-4">  </h1>
      <h1 className="text-lg text-red-500 mb-4">  </h1>

      <ul className="space-y-4">
        {reservations.map((res) => (
          <li key={res.id} className="p-4 border rounded bg-white shadow">
            <p><strong>:</strong> {res.name}</p>
            <p><strong>:</strong> {res.phone}</p>
            <p><strong>:</strong> {res.address} {res.detailAddress}</p>
            <p><strong>:</strong> {res.entrance}</p>
            <p><strong>:</strong> {res.date}</p>
            <p><strong>:</strong> {res.timeSlot}</p>
            <p><strong>:</strong> {res.items}</p>
            <p className="text-xs text-gray-500">: {res.createdAt?.toDate().toLocaleString()}</p>
          </li>
        ))}
      </ul>
    </div>
  );
}
